.categoryOverlay{
    position: fixed;
    right: 0;
    top: 0;
    bottom: 0;
    left: 0;
    background-color: rgba( 0 , 0, 0, 0.25);
    z-index: 3;
    display: none;
    transform-origin: top; 
    animation: pour-from-direction linear .05s;
    pointer-events: all;
}
.active {
    display: flex;
}